/* Page Three */
.page-three {
  display: flex;
  padding: 10% 5%;
  width: 100%;
}
@media only screen and (max-width: 480px) {
  .page-three {
    padding: 20% 5%;
    align-self: flex-start;
  }
}

.skill-group-container {
  display: grid;
  width: 70%;
  grid-template-columns: auto auto;
  grid-template-rows: auto auto;
  grid-gap: 50rem;
  justify-items: end;
  align-items: start;
}
@media only screen and (max-width: 480px) {
  .skill-group-container {
    display: grid;
    width: 50%;
    grid-template-columns: auto;
    grid-template-rows: auto auto auto auto;
    grid-gap: 50rem;
    justify-items: end;
    align-items: center;
  }
}

.skill-group {
  display: flex;
}

.skill-column {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  padding-right: 10rem;
  height: auto;
}

.skill-column p {
  font-size: 20rem;
  font-weight: 300;
  letter-spacing: 1rem;
  margin-bottom: 20rem;
  color: #c79766;
}

.skill-group .vertical-bar {
  width: 2rem;
  background: linear-gradient(to bottom, #eeb669 0%, #c97328 100%);
  height: auto;
  margin-right: 7rem;
}

.skill-column p:last-child {
  margin-bottom: 0;
}

.skill-column p:hover {
  color: #5b544f;
}

.group-name p {
  color: #5b544f;
  font-size: 15rem;
  font-weight: 700;
  writing-mode: vertical-rl;
  transition: color 0.2s ease-out;
}

.skill-svg-container {
  width: 50%;
  height: 100%;
  float: right;
  padding-left: 10%;
  animation: draw 1s both;
}
@media only screen and (max-width: 480px) {
  .skill-svg-container {
    padding-left: unset;
    overflow: visible;
    transform: rotateZ(90deg);
  }
}

.skill-svg-container text {
  animation: draw 10s linear forwards;
  font-size: 100rem;
  stroke-dasharray: 50;
  stroke-linejoin: round;
  stroke-linecap: round;
}

@keyframes draw {
  from {
    stroke-dasharray: 100;
  }
  50% {
    stroke-dasharray: 300;
  }
  to {
    stroke-dasharray: 1000;
  }
}
